import styles from "./Contents.module.scss";
import SolutionCards from "./Cards"
import SolutionChart from './Charts'

function Dashboard() {
  const name = 'O'

  return (
    <div>
      <div className={styles.card}>{name}  </div>

      <div className={styles.card_outline}>
        <p>PSG Analysis</p>
        <SolutionCards />
        <SolutionChart />
      </div>
    </div>
  );
}

export default Dashboard;