import styles from "./Products.module.scss";
import { Button } from '@mui/material';


function Products(props: any) {
  const custom_buttom = {
    marginTop: '40px',
    backgroundColor: '#283c59',
  }
  
  return (
    <>
      <div id="Products"></div>
      <div id={styles.Products}>
        <h1>Products</h1>
        <p>Humandeep   .</p>

        <div className={styles.content}>
          <img src="https://picsum.photos/350?random" alt="" />
          <div className={styles.text_box}>
            <p>(PSG)  </p>
            <h2>Well Sleep</h2>
            <p className={styles.text_box_articles}>    </p>

            <Button
              href="/hugo/solution"
              style={custom_buttom}
              variant="contained"

            >
               
            </Button>
          </div>
        </div>
      </div>
    </>

  );
}


